import React from 'react';
import PropTypes from 'prop-types';
import errorBoundary from '@src/components/errorBoundary';
import { FollowStore } from '../../../store';
import styles from './index.css';
import Link, { linkBase } from '@src/mobile/components/Link';
// import LoadingMore from '@src/mobile/components/LoadingMore';
import { getFollows } from '../../service';

const AllFollows = ({ statusBarHeight, hide }) => {
    const followStore = FollowStore.useContainer();
    const { followStates, changeFollowStates } = followStore;
    const { page, size, list, total } = followStates;

    const control = async () => {
        if ((page - 1) * size < total) {
            const res = await getFollows(page, size);
            const { pagination, list: newList } = res;
            const { total } = pagination;

            changeFollowStates({
                list: [...list, ...newList],
                total,
                page: page + 1,
            });

            return 'success';
        } else {
            return 'end';
        }
    };

    return (
        <div className={styles.wrap}>
            <div className={styles.nav} style={{ paddingTop: statusBarHeight }}>
                <div className={styles.navContent}>
                    <div
                        className={styles.back}
                        onClick={() => {
                            hide();
                        }}
                    />
                    <div className={styles.navTitle}>追踪列表</div>
                </div>
            </div>
            <div className={styles.list}>
                {list.map(item => {
                    const { id, title } = item;

                    return (
                        <div key={id} className={styles.item}>
                            <Link to={
                                  process.env.NODE_ENV === 'development' ?
                                  `${linkBase}/loc/timeline/event/${id}` :
                                     `${window.location.origin}/timeline/mobile_event.html`
                                
                            } className={styles.itemLink}>
                                <div className={styles.itemContent}>
                                    <div className={styles.itemTitle}>{title}</div>
                                </div>
                            </Link>
                        </div>
                    );
                })}
            </div>
            {/* <LoadingMore control={control} /> */}
        </div>
    );
};

AllFollows.propTypes = {
    statusBarHeight: PropTypes.number,
    hide: PropTypes.func,
};

export default errorBoundary(AllFollows);
