import { useEffect } from 'react';
import 'broadcastchannel-polyfill';
import { isGrounds, groundsClose } from '@src/public/utils/app';
import { BC_CLOSE_SAME_PAGE } from '@src/public/constants';

const isSupportClose = () => {
    const supportGv = '7.43.0';
    let isSupport = false;

    if (isGrounds) {
        const deviceInfo = window.grounds.getDeviceInfo();
        const os = deviceInfo.getOs();

        if (os.indexOf('ios') !== -1) {
            const gv = deviceInfo.getGv();
            const supportGvList = supportGv.split('.').map(item => parseInt(item));
            const gvList = gv.split('.').map(item => parseInt(item));

            for (const index in supportGvList) {
                if (supportGvList[index] !== gvList[index]) {
                    isSupport = gvList[index] > supportGvList[index];
                    break;
                } else if (index >= supportGvList.length - 1) {
                    isSupport = true;
                }
            }
        } else {
            isSupport = true;
        }
    }

    return isSupport;
};

// 端内关闭相同页面，防止层级过深
export const useCloseSamePage = () => {
    useEffect(() => {
        // 端内环境下
        if (isSupportClose()) {
            // 获取无查询参数和哈希值的URL
            const { origin, pathname } = window.location;
            const currentUrl = `${origin}${pathname}`;
            // 创建链接到命名频道的对象
            const bc = new BroadcastChannel(BC_CLOSE_SAME_PAGE);

            // 监听广播消息
            bc.onmessage = e => {
                const { data } = e;
                const { url } = data;

                if (url === currentUrl) {
                    groundsClose();
                }
            };

            bc.postMessage({ url: currentUrl });
        }
    }, []);
};
