import React from 'react';
import errorBoundary from '@src/components/errorBoundary';
import { FollowStore } from '../../store';
import styles from './index.css';
import Empty from './Empty';
import List from './List';

const Updates = () => {
    const followStore = FollowStore.useContainer();
    const { followStates } = followStore;
    const { initDone, list } = followStates;

    return <div className={styles.wrap}>{initDone && list.length <= 0 ? <Empty /> : <List />}</div>;
};

export default errorBoundary(Updates);
