import React, { useEffect } from 'react';
import PropTypes from 'prop-types';
import errorBoundary from '@src/components/errorBoundary';
import { userIsLogin, userLogin, isGrounds } from '@src/public/utils/app';
import BottomBar from '@src/mobile/components/BottomBar';
import { FollowStore, UpdatesStore } from '../store';
import styles from './index.css';
import Follow from './Follow';
import Updates from './Updates';
import { useCloseSamePage } from '@src/public/hooks/useCloseSamePage';

const Layout = ({ content }) => {
    useEffect(() => {
        if (isGrounds && !userIsLogin()) {
            userLogin();
        }
    }, []);

    useCloseSamePage();

    return (
        <div className={styles.layout}>
            <FollowStore.Provider>
                <UpdatesStore.Provider>
                    <Follow />
                    <Updates />
                    <BottomBar />
                </UpdatesStore.Provider>
            </FollowStore.Provider>
        </div>
    );
};

Layout.propTypes = {
    content: PropTypes.object,
};

export default errorBoundary(Layout);
