import { useState } from 'react';
import { createContainer } from 'unstated-next';

// 我的追踪
const useFollowStore = (
    initialState = {
        page: 1,
        size: 20,
        list: [],
        total: 0,
        initDone: false,
    },
) => {
    const [followStates, setFollowStates] = useState(initialState);
    const changeFollowStates = newStates => {
        setFollowStates(prevStates => Object.assign({}, prevStates, newStates));
    };

    return { followStates, changeFollowStates };
};

export const FollowStore = createContainer(useFollowStore);

