import axios from '@src/public/utils/request';

// 获取追踪列表
export const getFollows = (page, pageSize) => {
    return axios.get('/timeline/front/api/follows', {
        params: {
            page,
            size: pageSize,
        },
    });
};

// 获取追踪更新列表
export const getFollowsUpdates = (page, pageSize) => {
    return axios.get('/timeline/front/api/follows/updates', {
        params: {
            page,
            size: pageSize,
        },
    });
};
