import React, { useEffect, useState } from 'react';
import errorBoundary from '@src/components/errorBoundary';
import { FollowStore } from '../../store';
import styles from './index.css';
import { useMaximize } from '@src/public/hooks/app';
import { getFollows } from '../service';
import Link, { linkBase } from '@src/mobile/components/Link';
import AllFollows from './AllFollows';
import Header from './Header';

const limitFollow = 3;

const Follow = () => {
    const followStore = FollowStore.useContainer();
    const { followStates, changeFollowStates } = followStore;
    const { page, size, list } = followStates;

    const statusBarHeight = useMaximize();

    const [showAllFollows, setShowAllFollows] = useState(false);

    useEffect(() => {
        getFollows(page, size).then(res => {
            const { list, pagination } = res;
            const { total } = pagination;

            changeFollowStates({ list, page: page + 1, total, initDone: true });
        });
    }, []);

    return (
        <>
            <div className={styles.wrap}>
                <Header />
                {list.length ? (
                    <div className={styles.follows}>
                        <div className={styles.list}>
                            {list.slice(0, limitFollow).map(item => {
                                const { id, title } = item;

                                return (
                                    <div className={styles.item} key={id}>
                                        <Link to={
                                        process.env.NODE_ENV === 'development' ?
                                        `${linkBase}/loc/timeline/event/${id}` :
                                            `${window.location.origin}/timeline/mobile_event.html`
                                        } className={styles.itemLink}>
                                            <div className={styles.itemFlex}>
                                                <div className={styles.point} />
                                                <div className={styles.itemTitle}>{title}</div>
                                            </div>
                                        </Link>
                                    </div>
                                );
                            })}
                        </div>
                        {list.length > limitFollow ? (
                            <div
                                className={styles.more}
                                onClick={() => {
                                    setShowAllFollows(true);
                                }}>
                                <div className={styles.moreText}>查看全部</div>
                                <div className={styles.moreArrow} />
                            </div>
                        ) : null}
                    </div>
                ) : null}
            </div>
            {showAllFollows ? (
                <AllFollows
                    statusBarHeight={statusBarHeight}
                    hide={() => {
                        setShowAllFollows(false);
                    }}
                />
            ) : null}
        </>
    );
};

export default errorBoundary(Follow);
