import React, { useEffect, useState, useRef } from 'react';
import errorBoundary from '@src/components/errorBoundary';
import Link, { linkBase } from '@src/mobile/components/Link';
import styles from './index.css';

const Empty = () => {
    const wrapRef = useRef();

    const [height, setHeight] = useState('auto');

    useEffect(() => {
        const windowHeight = window.innerHeight;
        const documentHeight = document.documentElement.offsetHeight || document.body.offsetHeight;
        const diff = windowHeight - documentHeight;

        if (diff > 0 && wrapRef.current) {
            setHeight(diff + wrapRef.current.offsetHeight);
        }
    }, []);

    return (
        <div className={styles.wrap} ref={wrapRef} style={{ height }}>
            <div className={styles.content}>
                <div className={styles.icon} />
                <div className={styles.title}>您还没有追踪的事件</div>
                <Link to={
                      process.env.NODE_ENV === 'development' ?
                      `${linkBase}/loc/timeline` :
                         `${window.location.origin}/timeline/mobile_index.html`
                }>
                    <div className={styles.btn}>
                        <div className={styles.btnText}>查看热门事件</div>
                    </div>
                </Link>
                <div className={styles.comment}>追踪事件可查看相关进展</div>
            </div>
        </div>
    );
};

export default errorBoundary(Empty);
