import React from 'react';
import errorBoundary from '@src/components/errorBoundary';
// import LoadingMore from '@src/mobile/components/LoadingMore';
import { UpdatesStore } from '../../../store';
import styles from './index.css';
import { getFollowsUpdates } from '../../service';
import Item from './Item';

const List = () => {
    const updatesStore = UpdatesStore.useContainer();
    const { updatesStates, changeUpdatesStates } = updatesStore;
    const { page, size, list } = updatesStates;

    const control = async () => {
        try {
            const res = await getFollowsUpdates(page, size);
            const { pagination, list: newList } = res;
            const { total } = pagination;

            changeUpdatesStates({
                page: page + 1,
                list: [...list, ...newList],
                total,
            });

            if (page * size >= total) {
                return 'end';
            } else {
                return 'success';
            }
        } catch (e) {
            return 'error';
        }
    };

    return (
        <div className={styles.wrap}>
            <div className={styles.title}>
                <div className={styles.titleIcon} />
                <div className={styles.titleText}>正在更新</div>
            </div>
            <div className={styles.list}>
                {list.map(item => {
                    const { timeline, id } = item;
                    const { id: timelineId } = timeline;

                    return <Item content={item} key={`${timelineId}${id}`} />;
                })}
            </div>
            {/* <LoadingMore control={control} /> */}
        </div>
    );
};

export default errorBoundary(List);
