import React from 'react';
import classNames from 'classnames/bind';
import PropTypes from 'prop-types';
import errorBoundary from '@src/components/errorBoundary';
import Link, { linkBase } from '@src/mobile/components/Link';
import EventCard from '@src/mobile/components/EventCard';
import { useQrCodeUrl } from '@src/public/hooks/qrCode';
import { setAppQuery } from '@src/public/utils/url';
import { EVENT_LIST_MYFOLLOW } from '@src/public/constants';
import styles from './index.css';

const cx = classNames.bind(styles);

const Item = ({ content }) => {
    const { timeline } = content;
    const { id, title, label, allowComment } = timeline;

    let labelType = '';

    if (label === '新') {
        labelType = 'labelNew';
    } else if (label === '热') {
        labelType = 'labelHot';
    }

    // 分享链接
    const shareUrl = setAppQuery(`${linkBase}/loc/timeline/event/${id}`);
    // 生成二维码
    const qrcode = useQrCodeUrl({ url: shareUrl });

    return (
        <div className={styles.item} key={id}>
            <div className={styles.itemNav}>
                <div className={styles.itemTag}>
                    <Link className={styles.itemTagLink} to={
                         process.env.NODE_ENV === 'development' ?
                         `${linkBase}/loc/timeline/event/${id}` :
                            `${window.location.origin}/timeline/mobile_event.html`
                                
                        }>
                        <div className={styles.itemTagContent}>
                            {label ? <div className={cx('label', labelType)} /> : null}
                            <div className={styles.itemTitle}>{title}</div>
                        </div>
                    </Link>
                </div>
            </div>
            <EventCard
                content={content}
                timeline={{ id, title, allowComment, qrcode }}
                showType={EVENT_LIST_MYFOLLOW}
            />
        </div>
    );
};

Item.propTypes = {
    content: PropTypes.object,
};

export default errorBoundary(Item);
